import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    buttonType: '',
    editingPost: '',
    loggedUser: {
      id: 1,
      login: 'writer@mail.com',
      password: '123456',
      role: 'writer'
    },
    users: [
      {
        id: 1,
        login: 'writer@mail.com',
        password: '123456',
        role: 'writer'
      },
      {
        id: 2,
        login: 'reader@mail.com',
        password: '123456',
        role: 'reader'
      }
    ],
    posts: [
      {
        id: 1,
        title: ' ',
        description: '',
        claps: 0,
        createdAt: '2019-09-29T00:00:00.000Z',
        updateAt: '2019-09-29T00:00:00.000Z',
        userId: 1
      },
      {
        id: 2,
        title: ' ',
        description: '',
        claps: 0,
        createdAt: '2019-09-29T00:00:00.000Z',
        updateAt: '2019-09-29T00:00:00.000Z',
        userId: 1
      },
      {
        id: 3,
        title: ' a',
        description: '',
        claps: 0,
        createdAt: '2019-09-29T00:00:00.000Z',
        updateAt: '2019-09-29T00:00:00.000Z',
        userId: 1
      },
      {
        id: 4,
        title: ' a',
        description: '',
        claps: 0,
        createdAt: '2019-09-29T00:00:00.000Z',
        updateAt: '2019-09-29T00:00:00.000Z',
        userId: 1
      }
    ]
  },
  mutations: {
    SignUp (state, user) {
      state.users.push(user)
      state.loggedUser = { ...user }
    },
    logOut (state) {
      state.loggedUser = {}
    },
    logIn (state, user) {
      state.loggedUser = { ...user }
    },
    createPost (state, post) {
      state.posts.unshift(post)
    },
    deletePost (state, postID) {
      state.posts.splice(state.posts.findIndex((item) => item.id === postID), 1)
    },
    editPost (state, post) {
      const editingPost = state.posts.find((item) => item.id === state.editingPost)
      editingPost.title = post.title
      editingPost.description = post.description
      editingPost.updateAt = post.updateAt
      editingPost.userId = post.userId
    },
    clapPost (state, postID) {
      state.posts.find((item) => item.id === postID).claps++
    },
    rememberEditingPost (state, postID) {
      state.editingPost = postID
    },
    setButtonType (state, button) {
      state.buttonType = button
    },
    changePassword (state, newPassword) {
      state.loggedUser = newPassword
      state.users.find((item) => item.id === state.loggedUser.id).password = newPassword
    },
    loadPosts () {
    }
  },
  actions: {
    SignUp (context, user) {
      context.commit('SignUp', user)
    },
    logOut (context) {
      context.commit('logOut')
    },
    logIn (context, user) {
      context.commit('logIn', user)
    },
    createPost (context, post) {
      context.commit('createPost', post)
    },
    deletePost (context, postID) {
      context.commit('deletePost', postID)
    },
    editPost (context, post) {
      context.commit('editPost', post)
    },
    clapPost (context, postID) {
      context.commit('clapPost', postID)
    },
    rememberEditingPost (context, postID) {
      context.commit('rememberEditingPost', postID)
    },
    setButtonType (context, button) {
      context.commit('setButtonType', button)
    },
    changePassword (context, newPassword) {
      context.commit('changePassword', newPassword)
    },
    loadPosts (context) {
      context.commit('loadPosts')
    }
  }
})
